import React from 'react';
import { Row, Col, Card, CardBody, CardTitle, CardText } from 'reactstrap';
export const ResultSearch = (props) => (
	<Row>
		<Col lg='8' className='mx-auto'>
			<Card>
				<CardBody>
					<CardTitle> Location : 
						{ props.city && props.country && <i> { props.city},  { props.country } </i> } 
					</CardTitle> 
					<CardTitle> Temperature : {props.temperature && <i> { props.temperature }°</i>}  </CardTitle> 
					<CardTitle> Humidity : <i> { props.humidity } </i> </CardTitle> 
					<CardTitle> Windspeed : <i> { props.windspeed } </i> </CardTitle> 
					<CardTitle> Status : <i> { props.status } </i> </CardTitle> 
					<CardTitle> Description : <i> { props.description } </i> </CardTitle> 
				</CardBody>
			</Card>
		</Col>
	</Row>
)