import React, { Component } from 'react';
import {ResultSearch} from './Components/ResultSearch';
import {Container, Row, Col, Card, CardBody, Button, FormGroup, NavLink } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const API_KEY = "6cca5d9ad193e48a8fe2393bb7ce85d3";
const API_URL = "http://cors-anywhere.herokuapp.com/";

export class App extends Component {
  state = {
    city: undefined,
    country: undefined,
    temperature: undefined,
    humidity: undefined,
    windspeed: undefined,
    status: undefined,
    description: undefined

  }
  getWeather = async (e) => {
    e.preventDefault();
    const nameCity = this.nameCity.value;
    const api_call = await fetch(`https://api.openweathermap.org/data/2.5/find?q=${nameCity}&units=metric&appid=${API_KEY}`);
    const data = await api_call.json();
    console.log(data);

    this.setState({
      city: data.list[0].name,
      country: data.list[0].sys.country,
      temperature: data.list[0].main.temp,
      humidity: data.list[0].main.humidity,
      windspeed: data.list[0].wind.speed,
      status: data.list[0].weather[0].main,
      description: data.list[0].weather[0].description
    })

    this.addForm.reset();
  }
  render() {
    return (
      <div id='wrap'>
        <section className='weather-info'>
          <Container>
            <Row>
              <Col lg='8' className='mx-auto'>
                <div className='headingTitle'>
                  <h1> 
                    <FontAwesomeIcon icon="cloud-sun" />
                    WeatherInfo 
                  </h1>
                </div>
                <Card className='mb-3'>
                  <CardBody>
                    <form onSubmit={(e) => (this.getWeather(e))} className='form' ref={(input) => (this.addForm) = input}>
                      <Row form>
                        <Col md='10'>
                          <FormGroup>
                            <input type='teks' className='form-control' ref={(input) => (this.nameCity) = input} placeholder="Type your city..."/>
                          </FormGroup>
                        </Col>
                        <Col md='2'>
                          <FormGroup>
                            <Button block> 
                              <FontAwesomeIcon icon="search" />
                            </Button>
                          </FormGroup>
                        </Col>
                      </Row>
                    </form>
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <ResultSearch 
              city={this.state.city}
              country={this.state.country}
              temperature={this.state.temperature}
              humidity={this.state.humidity}
              windspeed={this.state.windspeed}
              status={this.state.status}
              description={this.state.description}
            />  
          </Container>
        </section>
        <footer className="footer">
          <Container fluid>
            <Row>
              <Col lg='6' md='6' sm='6'>
                <Card>
                  <CardBody>
                      <i> Copyright © </i>
                      <a href="https://portofolio-sandy.firebaseapp.com/" target="__blank"> sandy kurniawan </a>
                  </CardBody>
                </Card>
              </Col>
              <Col lg='6' md='6' sm='6'>
                <Card>
                  <CardBody>
                    <i> Powered: </i>
                      <a href="https://openweathermap.org/" target="__blank"> openweathermap.org </a>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </footer>
      </div>
    );
  }
}

